import Vue from 'vue';
import Router from 'vue-router';

// router configuration
import Home from './components/home.vue';
import About from './components/about.vue';
import NotFound from './components/not-found.vue';

// app component
import App from './app.vue';

const routes = [{
  path: '/',
  redirect: {
    name: 'home',
  },
}, {
  path: '/home',
  component: Home,
  name: 'home',
}, {
  path: '/about',
  component: About,
  name: 'about',
}, {
  path: '*',
  component: NotFound,
  name: 'page-not-found',
}];

Vue.use(Router);
const router = new Router({
  mode: 'history',
  routes,
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<app/>',
  components: { App },
  router,
});
